* {
  padding:0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  font-size: 14px;
}

body {
  background-color: #fafafa;
  color: #262626;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}



